@import url(https://fonts.googleapis.com/css2?family=Bai+Jamjuree:wght@400;600&display=swap);
body{
  margin: 0;
  padding: 0;
  width: 100%;
  box-sizing: border-box;
  font-family: 'Bai Jamjuree', sans-serif;
  overflow: none;
}


p{
  line-height: 1.5em;
  color: hsl(201, 11%, 66%);
}

h2{
  color: hsl(210, 10%, 33%);
}


.contianer{
  width: 100%;
  padding: 0;
  margin: 0;
  overflow: none;
}


@media (max-width: 820px){

.container{
  width: 100%;
  min-width: 290px;
  overflow: none;
}


}

.Header{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    width: 100%;
    padding-top: 5em;
    background-image: url(/clip/static/media/bg-header-desktop.df6601b0.png);
    background-size: cover;
    background-repeat: no-repeat;
}

img{
    width: 80px;
    margin-bottom: 1.5em;
}


h2{
    font-weight: 600;
    font-size: 2.5rem;
    margin: 0;
}

p{
    max-width: 35em;
    font-size: 1.2rem;
}

.header-article{
    margin-top: 4em;
}

.header-article h2{
    font-size: 2rem;
}

@media (max-width: 820px){
    
    .Header{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 100%;
        background-image: url(/clip/static/media/bg-header-mobile.80627651.png);
        background-size: cover;
        background-repeat: no-repeat;
    }
  
    h2{
        text-align: center;
        width: 90%;
    }

    p{
        text-align: center;
        width: 90%;
    }


    .header-article p{
        margin: 1em auto;
    }

}

.button-contianer{
    display: flex;
    grid-gap: 1em;
    gap: 1em;
}

button{
    padding: 1em 2em;
    border-radius: 2rem;
    border: none;
    margin-bottom: 2em;
    color: white;
    cursor: pointer;
    font-weight: 700;
    font-size: 1rem;
    align-items: center;
    margin: auto;
}

button:hover{
    color: black;
    background-color: white;
}


.ios{
    background-color: hsl(140, 66%, 44%);
}

.mac{
    background-color: hsl(233, 100%, 69%);
}


@media (max-width: 820px){

    .button-contianer{
        display: flex;
        flex-direction: column;
    }

    button{
        margin: 0;
        padding: 1em 5em;
        box-shadow: 1px 5px 0px 1px rgba(0, 0, 0, 0.219);
    }

}
.main-top-section{
    display: flex;
    justify-content: center;
    width: 100%;
    margin-top: 3em;
    grid-gap: 5em;
    gap: 5em;
}

.image-container{
    width: 50%;
    max-width: 700px;
    min-width: 450px;
}

.image-container img{
    width: 100%;
    min-width: 450px;
    overflow: hidden;
}


.top-content{
    padding-top: 4em;
    text-align: left;
}

 .top-content h2{
    font-size: 1.5rem;
    font-weight: bold;
    color: black;
}

.top-content p{
    font-size: 1rem;
    max-width: 70%;
}



@media (max-width: 820px){

    .main-top-section{
        display: flex;
        flex-direction: column;
        align-items: center;
        grid-gap: 0;
        gap: 0;
    }

    .image-container{
        display: flex;
        width: 100%;
        justify-content: center;
        align-items: center;
        align-content: center;
        grid-gap: 0;
        gap: 0;
    }

    .image-container img{
        width: 350px;
        max-width: 350px;
        min-width: 200px;
        grid-gap: 0;
        gap: 0;
    }
   
    .sub-container{
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: 2em;
    }



    .top-content p{
        font-size: 1.25rem;
        min-width: 90%;
        text-align: center;
    }


    .top-content h2{
        padding-top: 0;
        margin-top: 0;
        font-size: 1.75rem;
    }

}
.mainbot-container{
    margin-top: 7em;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}


article h2{
    font-size: 2rem;
}



.workflow-container{
    display: flex;
    align-items: center;
    margin: auto;
    width: 75%;
    grid-gap: 5em;
    gap: 5em;
    text-align: center;
    margin-top: 5em;
}


.workflow-container img{
    width: 60px;
}



@media (max-width: 820px){
    .workflow-container{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        align-content: center;
        width: 90%;
        
    }


    .workflow-container p{
        margin: auto;
    }




}
footer{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 2em 0 auto;
    width: 100%;
}

footer p{
    margin: 2em auto;
}

article{
    margin-top: 1em;
    text-align: center;
}

article h2{
    margin: auto;
}

img {max-width:100%;}

.logo-grid img{
    width: 120px;
    height: 100%;
}

.logo-grid{
display: flex;
flex-direction: row;
justify-content: space-between;
width: 75%;
margin: 7em auto;
}



nav{
    display: flex;
    margin-top: 10em;
    justify-content: center;
    width: 100%;
    height: 5em;
}


li, a{
    list-style: none;
    text-decoration: none;
    padding: .5em;
    font-weight: 400;
    font-size: 1.15rem;
    color: black;
}

.nav-links{
    display: flex;
    padding: .5em;
    width: 75%;
}


.social-icons img{
    width: 25px;
    padding: 1em;
    margin: auto;
    align-content: center;
    justify-content: center;
}

.nav-links img{
    width: 60px;
    align-items: center;
    justify-content: center;
    height: 3em;
}




@media (max-width: 820px){

    .logo-grid{
        display: flex;
        flex-direction: column;
        align-items: center;
        grid-gap: 2em;
        gap: 2em;
        width: 100%;
        margin-top: 9em;
        }

        nav{
            display: flex;
            flex-direction: column;
            align-items: center;
            text-align: center;
            margin-top: 20em;
        }

        li, a{
            list-style: none;
            text-decoration: none;
            padding: .5em;
            font-weight: 400;
            font-size: 1.15rem;
            color: black;
        }


        .nav-links{
            display: flex;
            flex-direction: column;
            align-items: center;
            margin: auto;
        }

}
